@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mdi::before {
    font-size: 24px;
    line-height: 14px;
    transition: transform 0.5s;
}
.mdi {
    transition: transform 1s;
}
.btn .mdi::before {
    position: relative;
    top: 4px;
}
.btn-xs .mdi::before {
    font-size: 18px;
    top: 3px;
}
.btn-sm .mdi::before {
    font-size: 18px;
    top: 3px;
}
.dropdown-menu .mdi {
    width: 18px;
}
.dropdown-menu .mdi::before {
    position: relative;
    top: 4px;
    left: -8px;
}
.nav .mdi::before {
    position: relative;
    top: 4px;
}
.navbar .navbar-toggle .mdi::before {
    position: relative;
    top: 4px;
    color: #FFF;
}
.breadcrumb .mdi::before {
    position: relative;
    top: 4px;
}
.breadcrumb a:hover {
    text-decoration: none;
}
.breadcrumb a:hover span {
    text-decoration: underline;
}
.alert .mdi::before {
    position: relative;
    top: 4px;
    margin-right: 2px;
}
.input-group-addon .mdi::before {
    position: relative;
    top: 3px;
}
.navbar-brand .mdi::before {
    position: relative;
    top: 2px;
    margin-right: 2px;
}
.list-group-item .mdi::before {
    position: relative;
    top: 3px;
    left: -3px
}

@font-face {
  font-family: 'Michroma';
  src: url(../../static/media/Michroma-Regular.c12a12d1.ttf)
}

body {
  color: white;
  background-color: #181717;
  font-family: 'Baloo Tammudu 2', cursive;
}

.results {
  width: 1010px;
}

.results th {
  color: #6a6363;
  border-bottom: 1px solid #6a6363;
}

.allcaps {
  color: rgb(73, 73, 82);
  text-decoration: none;
  letter-spacing: .1em;
  text-transform: uppercase;
}

.line-through {
  text-decoration: line-through;
}

.page-title {
  text-align: center;
  font-family: 'Michroma', sans-serif;
  margin: 30px;
  color: black;
  text-shadow: 0 2px 2px #3FE3BA;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  right: 20px;
  top: 20px;
  transition: right 0.5s;
  z-index: 20;
}

.filter.hide {
  right: -239px;
}
.filter input {
  width: 85px;
}

.input-container {
  width: 160px;
}

.hide-chevron {
  position: absolute;
  left: -35px;
}

.last-fetched {
  color: white;
  display: inline-block;
  min-width: 234px;
  text-align: center;
}

.fetch-result {
  margin: 50px 20px 20px 20px;
}

.float-right {
  float: right;
}

.inline-spacer {
  width: 0.5rem;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

