@import url('https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap');

@font-face {
  font-family: 'Michroma';
  src: url(assets/fonts/Michroma/Michroma-Regular.ttf)
}

body {
  color: white;
  background-color: #181717;
  font-family: 'Baloo Tammudu 2', cursive;
}

.results {
  width: 1010px;
}

.results th {
  color: #6a6363;
  border-bottom: 1px solid #6a6363;
}

.allcaps {
  color: rgb(73, 73, 82);
  text-decoration: none;
  letter-spacing: .1em;
  text-transform: uppercase;
}

.line-through {
  text-decoration: line-through;
}

.page-title {
  text-align: center;
  font-family: 'Michroma', sans-serif;
  margin: 30px;
  color: black;
  text-shadow: 0 2px 2px #3FE3BA;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  right: 20px;
  top: 20px;
  transition: right 0.5s;
  z-index: 20;
}

.filter.hide {
  right: -239px;
}
.filter input {
  width: 85px;
}

.input-container {
  width: 160px;
}

.hide-chevron {
  position: absolute;
  left: -35px;
}

.last-fetched {
  color: white;
  display: inline-block;
  min-width: 234px;
  text-align: center;
}

.fetch-result {
  margin: 50px 20px 20px 20px;
}

.float-right {
  float: right;
}

.inline-spacer {
  width: 0.5rem;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

